/* ./src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

@import "react-slideshow-image/dist/styles.css";

/* url("./assets/hero_filter.png") filter to place over the background image in first position and then the bg image */

@layer utilities {
  .hero-img-bg {
    background-image: url("./assets/hero_filter.png"),
      url("./assets/hero_bw.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }

  @keyframes slideInFromLeft {
    0% {
      transform: translateX(-100%);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes slideInFromRight {
    0% {
      transform: translateX(+300%);
    }
    100% {
      transform: translateX(0);
    }
  }

  .slidefromleft {
    /* This section calls the slideInFromLeft animation we defined above */
    animation: 1s ease-out 0s 1 slideInFromLeft;
  }

  .slidefromright {
    /* This section calls the slideInFromLeft animation we defined above */
    animation: 1s ease-out 0s 1 slideInFromRight;
  }

  @variants responsive {
    .text-shadow {
      text-shadow: 2px 2px 4px black;
    }
    .text-shadow-md {
      text-shadow: 4px 4px 8px black;
    }
    .text-shadow-lg {
      text-shadow: 15px 15px 30px black;
    }
    .text-shadow-none {
      text-shadow: none;
    }
  }
}
